import PropTypes from 'prop-types';
import Aside from './Aside';
import styled from 'styled-components';
import { links } from '../../../config';
import Icon from './Icons';

interface SocialProps {
    isHome?: boolean | undefined;
}

const Social: React.FC<SocialProps> = ({ isHome }) => {
    return (
        <Aside isHome={isHome} orientation="left">
            <SocialList>
                {links?.map(({ link, name }, i: number) => (
                    <li key={i}>
                        <a
                            href={link}
                            aria-label={name}
                            target="_blank"
                            rel="noreferrer"
                        >
                            <Icon name={name} />
                        </a>
                    </li>
                ))}
            </SocialList>
        </Aside>
    );
};

Social.propTypes = {
    isHome: PropTypes.bool,
};

export { Social };
export default Social;

const SocialList: any = styled.ul`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;

    &:after {
        content: '';
        display: block;
        width: 1px;
        height: 90px;
        margin: 0 auto;
        background-color: var(--light-slate);
    }

    li {
        &:last-of-type {
            margin-bottom: 20px;
        }

        a {
            padding: 10px;

            &:hover,
            &:focus {
                transform: translateY(-3px);
            }

            svg {
                width: 20px;
                height: 20px;
            }
        }
    }
`;
